<template>
  <div class="container p-0">
    <div class="row p-0">
      <div class="col-12 p-0" v-for="data in articleData" :key="data.id">
        <article-card :cardData="data"></article-card>
      </div>
      <div class="col-12 p-0 mt-3">
        <div class="container">
          <nav aria-label="Page navigation">
            <ul class="pagination justify-content-between align-items-center">
              <li class="h-100">
                <a
                  href="#"
                  class="left text-dark p-3 text-decoration-none"
                  ><small class="font-weight-bolder"></small></a
                >
              </li>
              <li class="h-100 rounded-lg">
                <ul class="d-flex text-dark justify-content-center">
                  <li class="p-3 bg-white shadow-sm">
                    1
                  </li>
                  <li class="p-3 bg-white shadow-sm">
                    {{pages.length}}
                  </li>
                </ul>
              </li>
              <li class="h-100">
                <a
                  href="#"
                  class="right text-dark p-3 text-decoration-none"
                  ><small class="font-weight-bolder"></small></a
                >
              </li>
            </ul>
          </nav>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import articleCard from "@/components/subcoms/ArticleCard";
export default {
  name: "",
  data: function () {
    return {
      articleData: [
        {
          id: 1,
          title: "",
          content: "",
          classify: "",
          pubdate: new Date().getFullYear(),
          pageview: 210,
        },
        {
          id: 5,
          title: "",
          content: "",
          classify: "",
          pubdate: new Date().getFullYear(),
          pageview: 10,
        },
        {
          id: 6,
          title: "",
          content: "",
          classify: "",
          pubdate: new Date().getFullYear(),
          pageview: 10,
        },
        {
          id: 7,
          title: "",
          content: "",
          classify: "",
          pubdate: new Date().getFullYear(),
          pageview: 10,
        },
        {
          id: 8,
          title: "",
          content: "",
          classify: "",
          pubdate: new Date().getFullYear(),
          pageview: 10,
        },
      ],
      pages: [
        { id: 1, link: "#", text: 1 },
        { id: 2, link: "#", text: 2 },
        { id: 3, link: "#", text: 3 },
        { id: 4, link: "#", text: 4 },
      ],
    };
  },
  components: {
    articleCard,
  },
  methods: {
    testGet() {
      this.$axios.get("/apis/article/queryall").then(
        function (response) {
          const data = response.data;
          this.articleData = [];
          data.forEach((article) => {
            this.articleData.unshift(article);
          });
        }.bind(this)
      );
    },
    testPost() {
      this.$axios
        .post("/apis/article/create", {
          title: "post",
          content: "post",
          classify: 1,
        })
        .then(
          function (response) {
            this.testGet();
          }.bind(this)
        );
    },
    testPut() {
      const id = 5;
      var pubdate = null;
      this.articleData.forEach((article) => {
        if (article.id === id) {
          pubdate = article.pubdate;
          this.$axios
            .put("/apis/article/update/" + id, {
              title: "put",
              content: "put",
              classify: 1,
              pubdate: pubdate,
            })
            .then(
              function (response) {
                this.testGet();
              }.bind(this)
            );
          return;
        }
      });
    },
    testDelete() {
      const id = this.articleData[this.articleData.length - 1].id;
      this.$axios.delete("/apis/article/delete/" + id).then((response) => {
        this.testGet();
      });
    },
  },
  computed: {
    newArticleData() {
      return JSON.parse(JSON.stringify(this.articleData));
    },
  },
  created() {
    // this.testGet();
  },
  watch: {
    // "this.newArticleData": {
    //     handler(newVal, oldVal){
    //         if (newVal !== oldVal) {
    //         }
    //     },
    //     deep: true
    // }
  },
};
</script>

<style scoped>
.pagination li a{
    box-shadow: 0 0 2px rgb(199, 199, 199);
    transition: .3s;
}
.pagination li a:hover{
    box-shadow: -2px -2px 2px rgb(223, 223, 223),
                2px 2px 2px rgb(163, 163, 163);
    transform: scale(1.01);
}
.pagination .left{
  border-radius: 0 20px 20px 0;
  background: rgba(255, 255, 255, 0.596);
  box-shadow: 3px 3px 5px rgb(110, 110, 110);
}
.pagination .right{
  border-radius: 20px 0 0 20px;
  background: rgba(255, 255, 255, 0.596);
  box-shadow: 3px 3px 5px rgb(110, 110, 110);
}
</style>
