<script>
    import MusicPlayer from './subcoms/MusicPlayer'

    export default {
        components: {
            MusicPlayer
        },
        name: "TopBar",
        data() {
            return {
                showArticleSubmenu: false,
                showAppletSubmenu: false,
                keywords: "",
                playFlag: false,
                classFlag: {
                    playBtn: false,
                    searchBtn: false,
                    input: false,
                },
                visualFlag: false,
                oldScrollTop: 0,
                scrollTop: 0,
                timer: null,
                width: 0,
                collapseIsShow: false
            };
        },
        methods: {
            handleScroll() {
                this.scrollTop = this.getScrollTop();
                if (this.scrollTop > this.oldScrollTop) {
                    if (document.body.offsetWidth < 992 && this.$refs.collapse.offsetLeft < 0) {
                        this.$refs.fixed.style.top = -this.$refs.fixed.offsetHeight * 2 + 'px';
                    } else if (document.body.offsetWidth < 992) {
                        this.$refs.fixed.style.top = 0;
                    } else {
                        this.$refs.fixed.style.top = -this.$refs.fixed.offsetHeight * 2 + 'px';
                    }
                } else {
                    this.$refs.fixed.style.top = 0;
                }
                this.oldScrollTop = this.scrollTop;
            },
            change() {
                this.collapseFlag = !this.collapseFlag;
                if (!this.collapseFlag) {
                    this.$refs.collapse.style.left = -this.$refs.collapse.offsetWidth + 'px';
                    this.$refs.btnIcon.style.transform = 'rotate(0)'
                } else {
                    this.$refs.collapse.style.left = 0;
                    this.$refs.btnIcon.style.transform = 'rotate(180deg)'
                }
            },
            // 
            submit() {
                this.$axios.get("").then((response) => {
                    console.log(response.data);
                })
            },
            // 
            getScrollTop() {
                var scrollTop = 0, bodyScrollTop = 0, documentScrollTop = 0;
                if (document.body) {
                    bodyScrollTop = document.body.scrollTop;
                }
                if (document.documentElement) {
                    documentScrollTop = document.documentElement.scrollTop;
                }
                scrollTop = (bodyScrollTop - documentScrollTop > 0) ? bodyScrollTop : documentScrollTop;
                return scrollTop;
            },
            screenDetection() {
                if (document.body.offsetWidth < 990) {
                    this.$refs.collapse.classList.add("navToLeft");
                    this.$refs.collapse.style.transition = '.5s';
                    this.$refs.collapse.style.paddingTop = this.$refs.navbar.offsetHeight - 1 + 'px';
                    this.$refs.collapse.style.left = -this.$refs.collapse.offsetWidth + 'px';
                    this.$refs.fixed.parentElement.children[2].onclick = () => {
                        if (this.$refs.collapse.offsetLeft >= 0) {
                            this.change();
                        }
                    }
                } else {
                    this.$refs.collapse.classList.remove("navToLeft");
                    this.$refs.collapse.style.paddingTop = null;
                    this.$refs.collapse.style.left = null;
                    this.$refs.fixed.parentElement.children[2].onclick = null;
                }
            }
        },
        mounted() {
            this.screenDetection();
            window.addEventListener("resize", this.screenDetection, false);
            window.addEventListener('scroll', this.handleScroll, false);
        }
    };
</script>

