<template>
    <div class="container d-none d-sm-block">
        <div class="row p-0 shadow bg-white rounded-lg mb-3">
            <div class="col-12">
                <div class="py-4 border-0">
                    <div class="row">
                        <div class="col">
                            <img src="../../assets/img/logo.png" width="60" height="60" class="rounded img mx-auto" alt="">
                        </div>
                    </div>
                    <div class="row">
                        <div class="col mt-4 justify-content-center">
                            <nav aria-label="breadcrumb">
                                <ol class="breadcrumb p-0 mb-0 bg-white text-center d-flex flex-row flex-nowrap justify-content-center align-items-center">
                                    <li class="breadcrumb-item" v-for="label in labels" :key="label.id">
                                        <small>
                                            <a href="#" class="rounded-lg d-flex flex-nowrap justify-content-center font-weight-bold" :style="{color: '#aaaaaa'}">
                                                <span class="col-lg-12 px-1"> {{label.text}} {{label.num}} </span>
                                            </a>
                                        </small>
                                    </li>
                                </ol>
                            </nav>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-12">
                <div class="card border-0 p-0 bg-white rounded">
                    <div class="card-body py-0 mb-4">
                        <blockquote class="blockquote mb-0 text-monospace">
                            <p><small v-text="quote.text"></small></p>
                            <footer class="blockquote-footer" v-cloak> <small>{{quote.author}}</small> <cite title="Source Title"><i><small>{{quote.product}}</small></i></cite></footer>
                        </blockquote>
                    </div>
                </div>
            </div>
        </div>
        <div class="row p-0 shadow rounded-lg mb-3">
            <div class="col p-0">
                <div class="border-0 card p-0 text-white bg-white">
                    <div class="card-header border-0 text-left"><a href="#" class="font-weight-bolder text-monospace text-secondary"></a></div>
                    <div class="card-body">
                        <div class="row">
                            <a class="col-12 p-2 mt-1 text-left d-flex border-bottom border-black-50 justify-content-between text-decoration-none"
                                v-for="classify in classifies"
                                :href="classify.href"
                                :key="classify.id">
                                <small class="font-weight-bolder" v-text="classify.text"></small>
                                <small class="font-weight-bolder" v-text="classify.num"></small>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="row p-0 shadow rounded-lg mb-3">
            <div class="col p-0">
                <div class="card border-0 p-0 text-white bg-white font-weight-bolder text-monospace">
                    <div class="card-header border-0 text-left text-secondary"></div>
                    <div class="card-body">
                        <div class="row">
                            <a class="col p-0 px-2 mt-1 mx-1 rounded-lg"
                                v-for="tag in tags"
                                :href="tag.href"
                                :key="tag.id"
                                :style="{backgroundColor: tag.backgrounColor, color: tag.color}">
                                <small class="font-weight-bolder" v-text="tag.text"></small>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div :class="['row', 'p-0', 'shadow', 'rounded-lg', 'mb-3', {'sr-only': !latestMessageShow}]">
            <div class="col p-0">
                <div class="card border-0 p-0 text-white bg-white font-weight-bolder text-monospace">
                    <div class="card-header border-0 text-left text-secondary"></div>
                    <div class="card-body px-3">
                        <div class="row">
                            <div class="col-12 p-0 pb-2 mt-2"
                                    v-for="msg in news"
                                    :key="msg.id"
                                    >
                                <div class="row mx-0">
                                    <div class="col-1 col-md-1 col-lg-2 p-0" ref="headProBox"><img ref="headPro" class="rounded-circle" :src="msg.headPro"></div>
                                    <div class="col-11 col-md-11 col-lg-10 p-0 align-items-center justify-content-start text-secondary">
                                        <div class="d-flex px-2 flex-row flex-wrap justify-content-between">
                                            <a :href="msg.vistor" class="text-decoration-none text-truncate text-info">
                                                <small class="font-weight-bold" v-text="msg.nickname"></small>
                                            </a>
                                            <small class="font-weight-bold"><i class="iconfont icon-riqi"></i><span class="text-black-50" v-text="msg.date"></span></small>
                                        </div>
                                        <a class="d-block p-0 px-2 text-left text-decoration-none text-secondary"
                                            :href="msg.href">
                                            <small class="font-weight-bolder" v-text="msg.text"></small>
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: '',
    data: function(){
        return {
            labels:[
                {id: null, text: "", num: 12, href: "#"},
                {id: null, text: "", num: 112, href: "#"},
                {id: null, text: "", num: 23, href: "#"},
            ],
            tags: [
                {id: null, text: "Three.js", href: "#", backgrounColor: "yellow", color: "red"},
                {id: null, text: "Vue.js", href: "#", backgrounColor: "yellow", color: "red"},
                {id: null, text: "Node.js", href: "#", backgrounColor: "yellow", color: "red"},
                {id: null, text: "SpirngBoot", href: "#", backgrounColor: "yellow", color: "red"},
                {id: null, text: "SpringFramework", href: "#", backgrounColor: "yellow", color: "red"},
                {id: null, text: "Mybatis", href: "#", backgrounColor: "yellow", color: "red"},
                {id: null, text: "Druid", href: "#", backgrounColor: "yellow", color: "red"},
            ],
            classifies: [
                {id: null, text: "", href: "#", num: 12},
                {id: null, text: "Java", href: "#", num: 12},
                {id: null, text: "JavaScript", href: "#", num: 12},
                {id: null, text: "", href: "#", num: 12},
                {id: null, text: "", href: "#", num: 12},
                {id: null, text: "", href: "#", num: 12},
            ],
            colors: [
                {backgroundColor: "#996699", color: "#FFCCCC"},
                {backgroundColor: "rgb(129 255 0)", color: "black"},
                {backgroundColor: "rgb(255, 0, 98)", color: "white"},
                {backgroundColor: "yellow", color: "rgb(255, 0, 98)"},
                {backgroundColor: "rgb(255, 0, 98)", color: "yellow"},
                {backgroundColor: "#0099CC", color: "#FF6666"},
                {backgroundColor: "#FFFF99", color: "#CCCCFF"},
                {backgroundColor: "#FF6666", color: "#FFFF66"},
                {backgroundColor: "#CCFFCC", color: "#CCCCFF"},
                {backgroundColor: "#66CCCC", color: "#CCFF66"},
                {backgroundColor: "#666699", color: "white"},
                {backgroundColor: "#FF9900", color: "#CC3399"},
            ],
            news: [
                {id: null, vistor: "#", nickname: "111", text: "", date: "2021.2.6", headPro: 'http://q1.qlogo.cn/g?b=qq&nk=1625562549&s=40', href: "#"},
                {id: null, vistor: "#", nickname: "2", text: "", date: "2021.2.6", headPro: 'http://q1.qlogo.cn/g?b=qq&nk=3570989569&s=40', href: "#"},
                {id: null, vistor: "#", nickname: "3", text: "", date: "2021.2.6", headPro: 'http://q1.qlogo.cn/g?b=qq&nk=3570989569&s=40', href: "#"},
            ],
            quote: {
                text: "",
                author: "",
                product: ""
            },
            windowX: null,
            latestMessageShow: true
        }
    },
    methods: {
        // 
        init(){
            // 
            this.labels.forEach((item, index) => {
                item.id = index;
            });
            this.classifies.forEach((item, index) => {
                item.id = index;
            });
            this.news.forEach((item, index) => {
                item.id = index;
            });
            for (var i = 0; i < this.tags.length; i++) {
                var tag = this.tags[i];
                var index = Math.floor(Math.random()*this.colors.length);
                tag.id = i;
                tag.color = this.colors[index].color;
                tag.backgrounColor = this.colors[index].backgroundColor;
            }
            // 
            this.windowX = window.innerWidth;
            // 
            window.addEventListener("resize", function(){
                this.windowX = window.innerWidth;
            }.bind(this), false);
        }
    },
    mounted(){
        this.init();
    },
    watch:{
        windowX: {
            handler(){
                if (this.windowX <= 992) {
                    this.latestMessageShow = false;
                } else {
                    this.latestMessageShow = true;
                }
            }
        }
    }
}
</script>

<style scoped>
.breadcrumb .breadcrumb-item::before{
    display: none;
    content: "";
}
[v-cloak]{
    display: none;
}
</style>
