<template>
  <div class="row px-3 text-monospace">
    <div class="col-12 music-play px-0 bg-white order-12">
      <div class="visual">
        <music-visual :musicInfo="musicInfo" @onended="onended"></music-visual>
      </div>
      <div class="play d-flex flex-column justify-content-between overflow-hidden">
        <div class="playing flex-fill pl-3 pr-2 d-flex align-items-center">
          <span class="d-block w-100 text-cetner text-truncate" v-text="played?played.name:''" :title="played?played.name:''"></span>
        </div>
        <div class="d-flex flex-fill border-top border-black-50" role="group" aria-label="Basic example">
          <button
            type="button"
            class="btn flex-fill p-0"
            @click="antiShake(false)"
          >
            <small><i class="iconfont icon-shangyishou"></i></small>
          </button>
          <button
            type="button"
            class="btn flex-fill p-0"
            @click="play"
            v-show="!isplaying"
          >
            <small><i class="iconfont icon-bofang"></i></small>
          </button>
          <button
            type="button"
            class="btn flex-fill p-0"
            @click="pause"
            v-show="isplaying"
          >
            <small><i class="iconfont icon-pause-full"></i></small>
          </button>
          <button
            type="button"
            class="btn flex-fill p-0"
            @click="antiShake(true)"
          >
            <small><i class="iconfont icon-xiayishou"></i></small>
          </button>
          <button
            type="button"
            class="btn flex-fill p-0"
            @click="showMusicList = !showMusicList"
          >
            <small><i class="iconfont icon-yinle"></i></small>
          </button>
        </div>
      </div>
    </div>
    <div class="music-list col-12 px-0 order-1 rounded-lg mb-2">
      <ul class="list my-2 p-0" v-show="showMusicList">
        <li class="overflow-hidden position-relative" :class="{'isplay': music.isPlay}" v-for="music in musicList" :key="music.id">
          <small class="font-weight-bold"
            ><a
              href="#"
              class="d-flex justify-content-between px-2 py-1 text-decoration-none"
              @click.prevent="playThis(music)"
              ><span v-text="music.id"></span
              ><span class="flex-fill text-left px-1" v-text="music.name"></span
              ><span v-text="music.singer"></span></a
          ></small>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import MusicVisual from "@/components/subcoms/MusicVisual";
export default {
  name: "",
  data: function () {
    return {
      mids: [],
      showMusicList: false,
      musicList: [
        {
          id: 1,
          name: "",
          uri: require("../../../static/1.mp3"),
          singer: "",
          isPlay: false,
        },
        {
          id: 2,
          name: "",
          uri: require("../../../static/2.mp3"),
          singer: "",
          isPlay: false,
        },
        {
          id: 3,
          name: "",
          uri: require("../../../static/3.mp3"),
          singer: "",
          isPlay: false,
        },
        {
          id: 4,
          name: "",
          uri: require("../../../static/4.mp3"),
          singer: "",
          isPlay: false,
        },
        {
          id: 5,
          name: "",
          uri: require("../../../static/5.mp3"),
          singer: "",
          isPlay: false,
        },
        {
          id: 6,
          name: "3",
          uri: require("../../../static/6.mp3"),
          singer: "",
          isPlay: false,
        },
        {
          id: 7,
          name: "Grandeur",
          uri: require("../../../static/7.mp3"),
          singer: "Snow Man",
          isPlay: false,
        },
        {
          id: 8,
          name: " () ()",
          uri: require("../../../static/8.mp3"),
          singer: "majiko",
          isPlay: false,
        },
        {
          id: 9,
          name: " ()",
          uri: require("../../../static/9.mp3"),
          singer: "",
          isPlay: false,
        },
      ],
      musicInfo: null,
      playlistId: 5038944749,
      played: null,
      isplaying: false,
      // 
      playId: 1,
      tiemr: null,
    };
  },
  computed: {},
  methods: {
    // 
    playThis(music) {
      if (this.played) {
        this.played.isPlay = false;
      }
      music.isPlay = true;
      this.musicInfo = music;
      this.played = music;
      this.isplaying = true;
      this.playId = this.played.id;
    },
    // 
    play() {
      if (this.played) {
        this.played.isPlay = true;
        this.musicInfo = this.played;
      } else {
        this.musicList[0].isPlay = true;
        this.played = this.musicList[0];
        this.musicInfo = this.musicList[0];
      }
      this.isplaying = true;
      this.playId = this.played.id;
    },
    // 
    pause() {
      if (this.played) {
        this.played.isPlay = false;
        this.musicInfo = this.played;
      }
      this.isplaying = false;
    },
    // 
    prev() {
      if (this.played) {
        this.played.isPlay = false;
        for (var i = 0; i < this.musicList.length; i++) {
          var music = this.musicList[i];
          if (music.id === this.playId) {
            this.playThis(music);
            break;
          }
        }
      } else {
        this.playThis(this.musicList[0]);
      }
    },
    // 
    next() {
      if (this.played) {
        this.played.isPlay = false;
        for (var i = 0; i < this.musicList.length; i++) {
          var music = this.musicList[i];
          if (music.id === this.played.id + 1) {
            this.playThis(music);
            break;
          }
        }
      } else {
        this.playThis(this.musicList[this.musicList.length - 1]);
      }
    },
    antiShake(flag) {
      if (flag) this.playId++;
      else this.playId--;
      if (this.tiemr) clearTimeout(this.tiemr);
      this.tiemr = setTimeout(() => {
        if (flag) {
          if (this.played.id === this.musicList.length) {
            this.playId = 1;
          }
          this.next();
        } else {
          if (this.played.id === 1) {
            this.playId = this.musicList.length;
          }
          this.prev();
        }
      }, 200);
    },
    onended() {
      // this.played.isPlay = false;
      this.next();
    },
  },
  components: {
    MusicVisual,
  },
  created() {},
  mounted() {
    // 
    // this.$axios
    //   .post("/api/playlist/detail?id=" + this.playlistId)
    //   .then((res) => {
    //     var trackIds = res.data.playlist.trackIds;
    //     trackIds.forEach((trackId) => {
    //       this.mids.push(trackId.id);
    //     });
    //     // url
    //     var song_url_url = "/api/song/url?id=";
    //     // 
    //     var song_detail_url = "/api/song/detail?ids=";
    //     for (var i = 0; i < this.mids.length; i++) {
    //       var id = this.mids[i];
    //       if (i === this.mids.length - 1) {
    //         song_detail_url += id;
    //         song_url_url += id;
    //         break;
    //       }
    //       song_url_url += id + ",";
    //       song_detail_url += id + ",";
    //     }
	//
    //     // 
    //     this.$axios.get(song_detail_url).then((detail) => {
    //       // 
    //       const detaicl_data = detail.data.songs;
    //       this.$axios.get(song_url_url).then((url) => {
    //         // url
    //         const url_data = url.data.data;
    //         // 
    //         for (var i = 0; i < detaicl_data.length; i++) {
    //           var musicInfo;
    //           for (var j = 0; j < url_data.length; j++) {
    //             if (url_data[j].id == detaicl_data[i].id) {
    //               musicInfo = {
    //                 id: this.musicList.length + 1,
    //                 name: detaicl_data[i].name,
    //                 singer: detaicl_data[i].ar[0].name,
    //                 uri: url_data[j].url,
    //                 isPlay: false,
    //               };
    //             }
    //           }
    //           this.musicList.push(musicInfo);
    //         }
    //       });
    //     });
    //   });
  },
};
</script>

<style scoped>
.isplay::before{
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 4px;
  background: #000000;
}
.music-play {
  border-radius: 30px;
}
.music-play .visual {
  position: relative;
  float: right;
  width: 80px;
  height: 60px;
  border-radius: 0 30px 30px 0;
  box-shadow: 0 0 3px rgb(0, 0, 0);
  overflow: hidden;
}
.music-play .play {
  height: 60px;
  margin-right: 82px;
  background: rgb(255, 255, 255);
  box-shadow: 0 0 3px rgb(0, 0, 0);
  border-radius: 30px 0 0 30px;
}
.music-play .play small {
  color: rgb(255, 255, 255);
  font-weight: bold;
}
.music-play .play .playing{
  font-size: xx-small;
  max-height: 50%;
  overflow: hidden;
  background: #ff0089;
  color: #fff;
  font-weight: bold;
}
.music-play .play .playing + div{
  background: #ea00ff;
}
.music-play .play button {
  height: 100%;
}
.music-list {
  box-shadow: 0 0 3px rgb(172, 172, 172);
  background-image: linear-gradient(0deg, #ffb9b991, #ff008c9f);
}
.list {
  overflow-y: scroll;
  max-height: 300px;
}
.list li a{
  color: rgb(31, 31, 31);
  transition: .3s;
}
.list li a:hover{
  background: #ff0089;
  transform: scale(1.02);
  border-radius: 5px;
  color: #fff;
}
</style>
