<template>
	<div id="app">
		<b-g></b-g>
		<keep-alive>
			<top-bar></top-bar>
		</keep-alive>
		<keep-alive>
			<jumbotron :text='pageDescribe'></jumbotron>
		</keep-alive>
		<div>
			<router-view></router-view>
			<page-footing></page-footing>
		</div>
	</div>
</template>

<script>
    import bG from './components/BG'
    import topBar from './components/TopBar'
    import pageFooting from './components/PageFooting'
    import jumbotron from './components/subcoms/Jumbotron'

    export default {
        name: 'App',
        data: function () {
            return {
                pageDescribe: {
                    title: 'MU_SHAN',
                    describe: '...',
                }
            }
        },
        components: {
            bG,
            topBar,
            pageFooting,
            jumbotron
        },
        created() {
            this.$root.$bus.$on(this.$route.fullPath.split("/")[1]+'PageDescribe', arg => {
                this.pageDescribe = arg;
            })
        },
        watch: {
            "$route.fullPath": {
                handler(newVal) {
                    const pathName = newVal.split("/")[1];
                    this.$root.$bus.$on(pathName+'PageDescribe', arg => {
                        this.pageDescribe = arg;
                    })
                }
            }
        }
    }
</script>

<style>
	[v-colak] {
		display: none;
	}

	#app {
		font-family: 'Avenir', Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-align: center;
		color: #000000;
	}


</style>
