<template>
  <div class="arcard-main mb-3 w-100" ref="card">
    <div class="container h-100">
      <div
        class="row h-100 mb-3 no-gutters rounded overflow-hidden flex-md-row position-relative"
      >
        <div class="date-tag position-absolute w-100 mt-2 text-white">
          <div
            class="w-100 d-flex justify-content-between align-items-center px-2"
          >
            <span>
              <i class="iconfont icon-biaoqian text-info"></i>
              <a
                href=""
                class="tag text-decoration-none text-black rounded-pill rounded-right px-3"
              >
                <small class="font-weight-bold">{{ cardData.classify }}</small>
              </a>
            </span>
            <span>
              <i class="iconfont icon-riqi text-info"></i>
              <a
                href=""
                class="date rounded-pill px-3 text-decoration-none"
                v-cloak
              >
                <small class="font-weight-bold">{{ cardData.pubdate }}</small>
              </a>
            </span>
          </div>
        </div>
        <div class="comment-praise-view position-absolute w-100 mb-2 text-white">
          <div
            class="w-100 d-flex align-items-center px-2"
          >
            <span class="rounded-pill px-3 text-info" v-cloak>
              <small class="font-weight-bold">({{cardData.pageview}})</small>
            </span>
            <span class="rounded-pill px-3 text-info">
              <small class="font-weight-bold">(1)</small>
            </span>
            <span class="rounded-pill px-3 text-info">
              <small class="font-weight-bold">(1)</small>
            </span>
          </div>
        </div>
        <div class="col-6">
          <a href="" class="title-content d-block position-relative h-100">
            <div class="main position-absolute py-5 w-100 h-100">
              <h3
                class="w-100 h-50 m-0 d-flex justify-content-center align-items-center text-dark"
              >
                {{ cardData.title }}
              </h3>
              <div class="mt-2 text-monospace">{{ cardData.content }}</div>
            </div>
          </a>
        </div>
        <div class="col-6">
          <div class="img h-100"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  props: ["cardData"],
  data: function () {
    return {
      height: 0,
      width: 0
    };
  },
  methods: {
    setWidth() {
      this.width = this.$refs.card.offsetWidth
      this.$refs.card.style.height = this.width / 2.5 + "px";
      this.height = this.$refs.card.offsetHeight;
    }
  },
  mounted() {
    this.setWidth();
  },
  watch: {
    'this.$refs.card.offsetWidth': {
      handler() {
        this.$refs.card.style.height = this.$refs.card.offsetWidth / 2.5 + "px";
      }
    }
  }
};
</script>

<style scoped>
.arcard-main {
  height: 180px;
}
.arcard-main .row{
  box-shadow: 0 0 5px rgb(202, 202, 202);
  transition: .5s;
}
.arcard-main .row:hover{
  box-shadow: -5px -5px 5px rgb(221, 221, 221),
              2px 2px 5px rgb(153, 153, 153);
  transform: scale(1.01);
}
.arcard-main .title-content {
  z-index: 1;
}
.arcard-main .date-tag,
.arcard-main .comment-praise-view{
  z-index: 2;
}
.arcard-main .comment-praise-view{
  bottom: 0;
}
.arcard-main .title-content::after {
  content: "";
  display: block;
  width: 200%;
  height: 100%;
  transform: scale(1.3, 2.2) perspective(10px) rotateX(0.8deg);
  transform-origin: left;
  background: rgba(255, 255, 255, .7);
  box-shadow: 0 0 5px rgb(212, 212, 212);
}
.arcard-main .title-content .main {
  z-index: 2;
