  text-indent: 2rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}

.arcard-main .date-tag .tag {
  background: yellow;
}
.arcard-main .date-tag .date {
  background: #fd0094;
  color: rgb(219, 219, 219);
}
.arcard-main .img {
