<template>
    <div id="editor" class="shadow-lg mb-2">
        <div class="col-12 p-0 d-flex justify-content-center">
            <div class="card text-secondary text-monospace border-0 text-left">
                <div class="card-body py-2">
                    <div class="card-header pt-4 border-0 bg-transparent">
                        <h4 class="text-center font-weight-bolder mb-0 align-middle" v-cloak>
                            {{cardTitle}}
                        </h4>
                    </div>
                    <div class="container">
                        <form method="post" ref="form">
                            <div class="row mb-3 justify-content-center align-items-center">
                                <div class="col-sm-12 col-md-4 col-lg-4 align-middle d-flex justify-content-center">
                                    <input name="nickname" v-model="form.nickname" class="py-1 px-2" type="text" placeholder="()" required>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 align-middle d-flex justify-content-center">
                                    <input name="email" v-model="form.email" class="py-1 px-2" type="email" placeholder="()" required>
                                </div>
                                <div class="col-sm-12 col-md-4 col-lg-4 align-middle d-flex justify-content-center">
                                    <input name="website" v-model="form.website" class="py-1 px-2" type="text" placeholder="">
                                </div>
                            </div>
                            <div class="row">
                                <div class="col">
                                    <textarea class="p-2 bg-transparent" v-model="form.content" style="resize:none" name="content" placeholder="...()"></textarea>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-sm-12 d-flex align-items-center col-lg-3">
                                    <a class="px-2 mx-1" href="#"></a>
                                    <a class="px-2 mx-1" href="#" @click.prevent="openFile"></a>
                                    <input type="file" ref="fileInput" v-show="false">
                                </div>
                                <div class="col-sm-12 col-lg-6 text-danger d-flex align-items-center"><span v-text="errorMsg"></span></div>
                                <div class="col-sm-12 col-lg-3">
                                    <button class="border-0 py-2 font-weight-bold shadow-sm rounded-lg" type="button" @click="reform"></button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: '',
    data: function(){
        return {
            form:{
                nickname: "",
                email: "",
                website: "",
                content: ""
            },
            errorMsg: "",
        }
    },
    props: ["cardTitle"],
    methods: {
        reform() {
            if (this.form.nickname.trim() !== "" && this.form.nickname !== null) {
                this.$refs.form.submit();
                this.errorMsg = "";
            } else {
                this.errorMsg = "";
            }
        },
        openFile() {
            this.$refs.fileInput.click();
        }
    },
}
</script>

<style scoped>
.card{
    width: 100%;
}
input{
    outline: none;
    border: none;
    border-bottom: 2px dashed rgb(204, 204, 204);
    transition: .2s;
    width: 95%;
    background: rgba(0, 0, 0, 0);
    text-align: center;
    box-sizing: border-box;
}
input:focus{
    border-bottom: 2px solid rgb(139, 139, 139);
}
textarea{
    height: 178px;
    border: 2px dashed rgb(204, 204, 204);
    outline: none;
    width: 100%;
    overflow-y: scroll;
    box-sizing: border-box;
    transition: .2s;
}
textarea:focus{
    border: 2px solid rgb(139, 139, 139)
}
#editor .card{
    background: url("../../assets/img/bg.png") right top no-repeat;
}
#editor form button{
    width: 100%;
    outline: none;
    color: rgb(139, 139, 139);
    background-color: rgba(0, 0, 0, 0);
    transition: .5s;
}
#editor form button:hover{
    color: rgb(255, 255, 255);
    background-color: rgba(0, 0, 0, .3);
}
</style>
