<template>
    <div class="jumbotron-box position-relative bg-transparent" ref="canvasBox">
        <div class="jumbotron jumbotron-fluid bg-transparent" ref="jumbotron">
            <div class="container bg-transparent">
                <h1 class="display-6 text-uppercase text-monospace font-weight-bold" style="font-size: 2.3rem" v-text="text.title"></h1>
                <p class="lead" v-text="text.describe"></p>
                <span class="d-inline-block text-center text-monospace text-justify"><input class="text-right" min="0" id="changeSprite" type="number" max="2000" v-model="nSprite"></span>
            </div>
        </div>
    </div>
</template>

<script>
import * as THREE from '@/assets/js/three.module.js'
export default {
    name: 'Jumbotron',
    data: function(){
        return {
            scene: null,
            camera: null,
            renderer: null,
            controls: null,
            ambient: null,
            sprites: null,
            mouseX: 0,
            mouseY: 0,
            windowHalfX: null,
            windowHalfY: null,
            w: null,
            h: null,
            nSprite: 200,
            spriteImgs: [
                require('../../assets/img/sprites/circle.png'),
                require('../../assets/img/sprites/snowflake7_alpha.png'),
            ],
            bgimg: require('../../assets/img/bg.png'),
            timer: null,
            scrollTop: 0
        }
    },
    methods: {
        init() {
            this.w = this.$refs.jumbotron.offsetWidth;
            this.h = this.$refs.jumbotron.offsetHeight;
            this.scene = new THREE.Scene();
            this.camera = new THREE.PerspectiveCamera(45, this.w / this.h, 200, 10000);
            this.camera.position.set(0, 0, 800);
            this.renderer = new THREE.WebGLRenderer({
                alpha: true
            });
            this.renderer.setClearAlpha(0.0);
            this.renderer.setSize(this.w, this.h);
            this.ambient = new THREE.AmbientLight(0xfffff);
            this.scene.add(this.ambient);
            this.$refs.canvasBox.appendChild(this.renderer.domElement);
            this.$refs.canvasBox.children[1].style.position = "absolute";
            this.$refs.canvasBox.children[1].style.top = "0";

            this.makeSprites();

            window.addEventListener('resize', this.onResize);
            document.body.addEventListener("mousemove", this.onPointerMove);
        },
        onResize(){
            this.setHeight();
            this.w = this.$refs.jumbotron.offsetWidth;
            this.h = this.$refs.jumbotron.offsetHeight;
            this.windowHalfX = this.w/2;
            this.windowHalfY = this.h/2;
            this.camera.aspect = this.w / this.h;
            this.makeSprites();
            // 
            this.camera.updateProjectionMatrix();
            this.renderer.setSize( this.w, this.h );
            this.render();
        },
        onPointerMove( event ) {
            if ( event.isPrimary === false ) return;
            this.mouseX = event.clientX - this.windowHalfX;
            this.mouseY = event.clientY - this.windowHalfY;
        },
        render() {
            this.camera.position.x += (this.mouseX - this.camera.position.x) * 0.05;
            this.camera.position.y += (-this.mouseY - this.camera.position.y) * 0.05;
            this.renderer.render(this.scene, this.camera);
            requestAnimationFrame(this.render);
            this.animate();
            this.camera.lookAt(this.scene.position);
        },
        makeSprites() {
            this.scene.remove(this.sprites);
            this.renderer.clear();
            this.sprites = new THREE.Group();
            var sprite, material;
            var loader = new THREE.TextureLoader();
            var maps = [
                    loader.load(this.spriteImgs[0]),
                    loader.load(this.spriteImgs[1])
            ];
            var x = this.$refs.canvasBox.offsetWidth;
            for (var i = 0; i < this.nSprite; i++) {
                material = new THREE.SpriteMaterial({
                    map: maps[this.getRandom(0, 1)],
                    color: this.getRandomColor().getHex()
                });
                sprite = new THREE.Sprite(material);
                sprite.scale.set(10, 10, 1);
                sprite.position.set(
                    this.getRandom(-x/1.2,x/1.2),
                    this.getRandom(-x/1.2,x/1.2),
                    this.getRandom(-x/2,x));
                this.sprites.add(sprite);
            }
            this.scene.add(this.sprites);
        },
        animate() {
            var time = Date.now() / 10000;
            this.sprites.rotateX(Math.cos(time) * 0.0005);
            this.sprites.rotateY(Math.sin(time) * 0.0005);
            this.sprites.rotateZ(Math.sin(time) * 0.0005);
        },
        getRandomColor() {
            return new THREE.Color(this.getRandom(0, 255), this.getRandom(0, 255), this.getRandom(0, 255));
        },
        getRandom(min, max) {
            return Math.floor(Math.random() * ( max-min ) + 1) + min;
        },
        setHeight() {
            this.$refs.jumbotron.style.height = window.innerHeight + 'px';
        }
    },
    props: [
        'text'
    ],
    mounted(){
        console.log('init...');
        this.setHeight();
        this.init();
        this.render();
    },
    computed:{
        newNSprite: function(){
            return JSON.parse(JSON.stringify(this.nSprite));
        }
    },
    watch:{
        nSprite: function(newval){
            this.nSprite = parseInt(newval);
            if(this.timer) {
                clearTimeout(this.timer);
            }
            this.timer = setTimeout(() => {
                this.makeSprites();
            }, 1000);
        }
    }
}
</script>

<style scoped>
.jumbotron{
    position: relative;
    display: flex;
    /* background: url("../../assets/img/jb_bg.jpg") center no-repeat; */
    background: url("../../assets/img/bg2.png") center no-repeat;
    background-size: cover;
}
.jumbotron .container{
    display: flex;
    z-index: 10;
    flex-direction: column;
    justify-content: center;
    align-content: center;
}
canvas{
    position: absolute;
    top: 0;
    left: 0;
}
#changeSprite{
    width: 60px;
    background: rgba(0, 0, 0, 0);
    border: none;
    outline: none;
    color: purple;
}
.jumbotron-box::after{
    position: absolute;
    bottom: 1px;
    left: 0;
    background: #fefefe;
    content: "";
    width: 100%;
    height: 1px;
    box-shadow: 0 0 20px 20px #fefefe;
}
</style>
