<template>
  <div id="music-visual" ref="musicVisual">
    <div class="canvasBox" ref="canvas"></div>
  </div>
</template>

<script>
import * as THREE from "@/assets/js/three.module.js";
export default {
  data: function () {
    return {
      scene: null,
      camera: null,
      renderer: null,
      ambient: null,
      analyser: null,
      group: null,
      light: null,
      w: 80,
      h: 60,
      listener: null,
      audio: null,
      audioLoader: null,
      arr: [],
      duraction: 0,
    };
  },
  props: ["musicInfo"],
  methods: {
    // 
    init() {
      this.scene = new THREE.Scene();
      this.camera = new THREE.PerspectiveCamera(45, this.w / this.h, 1, 1000);
      this.renderer = new THREE.WebGLRenderer({
        antialias: true,
      });
      this.renderer.setClearColor(0x0);
      this.renderer.setSize(this.w, this.h);
      this.$refs.canvas.appendChild(this.renderer.domElement);
      this.ambient = new THREE.AmbientLight({ color: 0xffffff });
      this.scene.add(this.ambient);
      // 
      this.addLights();
      // 
      this.addObj();
      // 
      this.render();
      this.$refs.musicVisual.addEventListener("resize", this.onResize);
    },
    // 
    addObj() {
      // 
      this.group = new THREE.Group();
      // 
      var plane = new THREE.Mesh(
        new THREE.PlaneGeometry(
          window.innerWidth * 2,
          window.innerHeight * 2,
          1,
          1
        ),
        new THREE.MeshPhongMaterial({
          color: 0x0,
          specular: 0x191919,
          shininess: 0,
        })
      );
      plane.rotateX(-Math.PI / 2);
      this.scene.add(plane);
      // 
      var box;
      var nBox = 36; // 
      for (let i = 0; i < nBox; i++) {
        box = new THREE.Mesh(
          new THREE.BoxGeometry(1, 2, 1),
          new THREE.MeshPhongMaterial({
            color: 0x8a00ff,
            emissive: 0x0,
            specular: 0xff00e5,
            shininess: 100,
          })
        );
        // 
        box.position.set(Math.sin(0.175 * i) * 20, 0, Math.cos(0.175 * i) * 20);
        this.group.add(box);
      }
      this.scene.add(this.group);
      // 
      // scene.add(new THREE.AxesHelper(500));
    },
    // 
    addLights() {
      this.light = new THREE.PointLight(
        new THREE.Color(
          this.getRandom(0, 255),
          this.getRandom(0, 255),
          this.getRandom(0, 255)
        ),
        2,
        100,
        2
      );
      this.light.position.set(0, 0.5, 0);
      this.scene.add(this.light);
    },
    // 
    getRandom(min, max) {
      return Math.floor(Math.random() * (max - min) + 1) + min;
    },
    // 
    loadAudio() {
      // 
      this.listener = new THREE.AudioListener();
      // 
      this.audio = new THREE.Audio(this.listener);
      this.camera.add(this.listener);
      // 
      this.audioLoader = new THREE.AudioLoader();
      if (this.musicInfo) {
        this.audioLoader.load(
          this.musicInfo.uri,
          function (buffer) {
            this.audio.setBuffer(buffer);
            this.audio.setLoop(false);
            this.audio.setVolume(1);
            this.audio.playbackRate = 1;
            this.audio.onEnded = function() {
              console.log("onEnded");
              this.audio.isPlaying = false;
              this.$emit("onended");
            }.bind(this);
            // , , 
            if (this.musicInfo.isPlay) {
              this.audio.play(0);
            }
            // 
            this.analyser = new THREE.AudioAnalyser(this.audio);
          }.bind(this)
        );
      }
    },
    onResize() {
      this.w = this.$refs.musicVisual.offsetWidth;
      this.h = this.$refs.musicVisual.offsetHeight;
      this.camera.aspect = this.w / this.h;
      this.camera.updateProjectionMatrix();
      this.renderer.setSize(this.w, this.h);
      this.renderer.render(this.scene, this.camera);
    },
    // 
    render() {
      // 
      window.requestAnimationFrame(this.render);
      // 
      var time = Date.now() / 10000;
      this.camera.position.set(
        Math.sin(time) * 50,
        Math.sin(time) * 20 + 30,
        Math.cos(time) * 50
      );
      this.camera.lookAt(0, 0, 0);
      // 
      if (this.analyser) {
        // 
        var freqArr = this.analyser.getFrequencyData();
        // 
        var objs = this.group.children;
        // 
        for (var i = 0; i < objs.length; i++) {
          if (freqArr[i] === 0) {
            objs[i].scale.y = 1;
          } else {
            // y
            objs[i].scale.y = freqArr[i] / 30;
            // 
            objs[i].material.color.r = freqArr[i] / 200;
          }
        }
        if (this.light.color.r >= 255) {
          this.light.color.r = 0;
        }
        if (this.light.color.g >= 255) {
          this.light.color.g = 0;
        }
        if (this.light.color.b >= 255) {
          this.light.color.b = 0;
        }
        this.light.color.r += Math.sin(time * 0.75) + 1;
        this.light.color.g += Math.sin(time * 0.5) + 1;
        this.light.color.b += Math.sin(time * 0.25) + 1;
      }
      this.renderer.render(this.scene, this.camera);
    },
  },
  mounted() {
    this.init();
    if (this.musicInfo) {
      this.loadAudio(this.musicInfo);
    }
  },
  computed: {
    newMusicInfo() {
      return JSON.parse(JSON.stringify(this.musicInfo));
    },
  },
  watch: {
    newMusicInfo: {
      handler(newVal, oldVal) {
        if (newVal && oldVal) {
          if (newVal.id === oldVal.id && this.audio) {
            if (newVal.isPlay) {
              this.audio.play();
            } else {
              this.audio.pause();
            }
          } else if (this.audio) {
            this.audio.stop();
            this.loadAudio();
          } else {
            this.loadAudio();
          }
        } else {
          this.loadAudio();
        }
      },
      deep: true,
    },
  },
};
</script>

<style scoped>
.canvasBox {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0);
}
#music-visual {
  position: absolute;
  width: 80px;
  height: 60px;
  box-shadow: 0 0 5px #000;
  background: #000;
  overflow: hidden;
}
</style>
